<template>
    <div v-for="i in nbMesures" :key="i" style="display: inline-block; height: 4rem; margin-right: 0.5vw" :class="{start_mesure: i==1, border_right_mesures : i != 4}"> 
        <Note  v-for="n in nbTemps" :key="n+nbTemps*(i-1)" :indexNbSub="n" :indexNbTemps="i" :nbTemps="nbTemps" :idSample="id" />
    </div>
    <div style="margin-right: 10px"> 
        <input type="number" id="temps" name="temps" min="1" max="8" v-model.number="temps" style="width:2rem">
    </div>
    <button v-if="$store.state.samplesChosen.length!==1" type="button" class="btn btn-danger" @click="deleteSample">
        <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-trash" viewBox="0 0 16 16">
            <path d="M5.5 5.5A.5.5 0 0 1 6 6v6a.5.5 0 0 1-1 0V6a.5.5 0 0 1 .5-.5zm2.5 0a.5.5 0 0 1 .5.5v6a.5.5 0 0 1-1 0V6a.5.5 0 0 1 .5-.5zm3 .5a.5.5 0 0 0-1 0v6a.5.5 0 0 0 1 0V6z"/>
            <path fill-rule="evenodd" d="M14.5 3a1 1 0 0 1-1 1H13v9a2 2 0 0 1-2 2H5a2 2 0 0 1-2-2V4h-.5a1 1 0 0 1-1-1V2a1 1 0 0 1 1-1H6a1 1 0 0 1 1-1h2a1 1 0 0 1 1 1h3.5a1 1 0 0 1 1 1v1zM4.118 4 4 4.059V13a1 1 0 0 0 1 1h6a1 1 0 0 0 1-1V4.059L11.882 4H4.118zM2.5 3V2h11v1h-11z"/>
        </svg>
    </button>
</template>

<script>
import Note from '@/components/Note.vue'


export default ({
    data(){
        return {
            nbTemps: 4,
            nbMesures: 4,
        }
    },
    components: {
        Note
    },
    methods:{
        deleteSample(){
            this.$store.commit('deleteSamplesChosen',this.id);
        },
        selectChannel(){
            this.$store.commit('selectChannel', this.mySample.id);
        },
    },
    props: ['id', 'name', 'src'],
    computed: {
        getAudioCtx(){
            return this.$store.getters.getAudioCtx;
        },
        mySample(){
            const chosenSample = this.$store.state.samplesChosen.find(sample => sample.id===this.id);
            return chosenSample;
        },
        temps:{
            get(){
                return this.nbTemps;
            },
            set(newNbTemps){
                if (newNbTemps===5){
                    if (this.nbTemps===6){
                        newNbTemps = 4 ;
                    }
                    else{
                        newNbTemps = 6;
                    }    
                }
                else if (newNbTemps===7){
                    if (this.nbTemps===8){
                        newNbTemps = 6 ;
                    }
                    else{
                        newNbTemps = 8;
                    }   
                }
                else if(newNbTemps===""){
                    return;
                }
                var divsActivate = document.getElementsByName(this.name);
                for(var i=0; i<divsActivate.length; i++){
                    divsActivate[i].classList.remove("active");
                }
                this.nbTemps = newNbTemps;
            }
        }
    },
})
</script>

