    border-right: 0.2rem solid black; height: 3rem;
}

.border_bottom{
    border-bottom: 0.1rem solid white
}

.margin_left{
    margin-left: 1rem;
}

.start_mesure{
    margin-left: 0.5rem;
}
.hidden{
    display: none;
}
